<template>
    <div class="remark">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>订单备注</span>
        </header>
        <section class="s1">
            <div class="s11">快速备注</div>
            <ul>
                <li v-for="(r,i) in remarks">
                    <span v-for="(m,n) in r" :class="{styleS:rem[rem.indexOf(i+''+n)]==i+''+n}" @click="zzz(i,n)">{{m}}</span>
                </li>
            </ul>
        </section>
        <section class="s2">
            <div class="s11">其他备注</div>
            <textarea type="text" placeholder="请输入备注内容(可不填)" v-model="value"></textarea>
        </section>
        <section class="s3" @click="toLast">确定</section>
    </div>
</template>

<script>
    export default {
        name: "Remark",
        data(){
            return{
                remarks:[],
                value:"",
                rems:[],
                remark:[],
                rem:[],
            }
        },
        created(){
           this.axios.get("/v1/carts/128533/remarks").then(res=>{
               console.log(res.remarks)
               this.remarks = res.remarks;
           });
        },
        methods:{
            zzz(i,n){
                let isShow = true;
                if (this.rem.length<1){
                    this.rem.push(i+""+n);
                    this.rems.push(this.remarks[i][n])
                    isShow = false;
                } else{
                    for(let z =0;z<this.rem.length;z++){
                        if(this.rem[z].toString().split("")[0] == i && this.rem[z].toString().split("")[1] == n){
                            this.rem.splice(this.rem.indexOf(i+""+n),1);
                            this.rems.splice(this.rem.indexOf(i+""+n),1);
                            isShow = false;
                        }else if(this.rem[z].toString().split("")[0] == i){
                            this.rem.splice(z,1);
                            this.rems.splice(z,1);
                            this.rem.push(i+""+n);
                            this.rems.push(this.remarks[i][n])
                            isShow = false;
                        }
                    }
                }
                if (isShow){
                    this.rem.push(i+""+n);
                    this.rems.push(this.remarks[i][n])
                }
                console.log(this.rem)
                console.log(this.rems)
            },
            toLast(){
                this.remark = "";
                for (let i=0;i<this.rems.length;i++) {
                    if (i == 0){
                        this.remark += this.rems[i];
                    } else{
                        this.remark += "、" + this.rems[i];
                    }
                }
                if(this.value != ""){
                    this.remark += "、" + this.value;
                }
                localStorage.setItem("remark",this.remark);
                console.log(this.remark)
                this.$router.go(-1)
            },
        }
    }
</script>

<style scoped>
    .remark{
        width: 100%;
        padding-top: 1.95rem;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .s1,.s2{
        background-color: #fff;
        margin-top: .4rem;
        padding: 0 .6rem 1rem;
    }
    .s11{
        font-size: .65rem;
        color: #333;
        line-height: 2rem;
    }
    .s2>textarea{
        width: 93%;
        background-color: #f9f9f9;
        border: .025rem solid #eee;
        resize: none;
        min-height: 3.5rem;
        border-radius: .2rem;
        font-size: .6rem;
        color: #666;
        padding: .5rem;
        outline: none;
    }
    .s3{
        background-color: #4cd964;
        font-size: .7rem;
        color: #fff;
        text-align: center;
        margin: 0 .7rem;
        line-height: 1.8rem;
        border-radius: .2rem;
    }
    .s1 ul{
        display: flex;
        flex-wrap: wrap;
    }
    .s1 li{
        margin-right: .6rem;
        margin-bottom: 1rem;
    }
    .s1 span{
        font-size: .6rem;
        color: #333;
        padding: .3rem .6rem;
        border: .025rem solid #3190e8;
        border-left: 0;
    }
    .s1 li span:nth-child(1){
        border-left: .025rem solid #3190e8;
        border-top-left-radius: .2rem;
        border-bottom-left-radius: .2rem;
    }
    .s1 li span:last-child{
        border-top-right-radius: .2rem;
        border-bottom-right-radius: .2rem;
    }
    .styleS{
        background-color: #3190e8;
        color:#fff;
    }
</style>